<template>
  <v-app id="inspire">
    <v-app-bar
      app
      color="white"
      flat
    >
      <v-container class="py-0 fill-height">
        <v-avatar
          class="mr-10"
          color="blue darken-1"
          size="32"
        >
		<v-icon dark>mdi-weather-cloudy</v-icon>
		</v-avatar>

        <h1
		  class="text-h4"
          v-for="link in links"
          :key="link"
          text
        >
          {{ link }}
        </h1>

        <v-spacer></v-spacer>

        <v-responsive max-width="260">
			<v-form v-on:submit.prevent="fetchWeather(key,searchCity)">
				<v-text-field
					dense
					hide-details
					rounded
					v-model="searchCity"
					placeholder="Search city"
					append-icon="mdi-magnify"
				></v-text-field>

			</v-form>
        </v-responsive>
      </v-container>
    </v-app-bar>

    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col>
            <v-sheet
              min-height="70vh"
              rounded="lg"
			  class="d-flex justify-center align-center"
            >
              <!--  -->
				<v-lazy>
					<v-card
						v-if="lastCity"
						class="mx-auto"
						max-width="700"
						>
						<v-list-item two-line>
						<v-list-item-content>
							<v-list-item-title class="headline">
							{{lastCity}}
							</v-list-item-title>
							<v-list-item-subtitle class="mr-5">{{getDay}}, {{getHour}}:{{getMinute}}, {{getDescription}}</v-list-item-subtitle>
						</v-list-item-content>
						</v-list-item>

						<v-card-text>
						<v-row align="center">
							<v-col
							class="display-3"
							cols="7"
							>
							{{getDegrees}}&deg;C
							</v-col>
							<v-col cols="5">
							<v-img
								:src=getImage
								width="94"
							></v-img>
							</v-col>
						</v-row>
						</v-card-text>

						<v-list-item>
						<v-list-item-icon>
							<v-icon>mdi-send</v-icon>
						</v-list-item-icon>
						<v-list-item-subtitle>{{getWindSpeed}} km/h</v-list-item-subtitle>
						</v-list-item>

						<v-list-item>
						<v-list-item-icon>
							<v-icon>mdi-cloud-download</v-icon>
						</v-list-item-icon>
						<v-list-item-subtitle>{{humidity}}%</v-list-item-subtitle>
						</v-list-item>
					</v-card>
				</v-lazy>
				<v-snackbar
					v-if="!lastCity"
					v-model="snackbar"
					color="red lighten-1"
					>
					<v-icon
					color="white">
						mdi-exclamation
					</v-icon>
					{{ text }}
					<template v-slot:action="{ attrs }">
						<v-btn
						color="white"
						text
						v-bind="attrs"
						@click="snackbar = false"
						>
						Close
						</v-btn>
					</template>
				</v-snackbar>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
	import axios from 'axios'
	export default {
		data: () => ({
			searchCity: "",
			key: "6bbfd037777553d7e02b0d92811aa2f7",
    		links: [
    			'Live Weather'
			],
			humidity: 0,
			windSpeed: 0,
			degrees: 0,
			description: "",
			errorMsg: "Errore",
			lastCity: "",
			icon: "",
			snackbar: false,
      		text: `Error finding this city`,
		}),
		methods: {
			async fetchWeather(key,city){
				try {
					var result = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${key}`);
					console.log(result.data);
					if(result.data){
						this.icon = result.data.weather[0].icon;
						this.humidity = result.data.main.humidity;
						this.windSpeed = result.data.wind.speed;
						this.degrees = result.data.main.temp;
						this.description = result.data.weather[0].description;
						this.lastCity = result.data.name;
						console.log(this.main + " " + this.description);
					}
				} catch (error) {
					this.snackbar = true;
				}
			}
		},
		computed: {
			updateCity: (name) => {this.lastCity = name},
			getDay: () => {
				var d = new Date();
				 switch(d.getDay()){
					case 1:
						return "Monday";
					break;
					case 2:
						return "Thursday";
					break;
					case 3:
						return "Wednesday";
					break;
					case 4:
						return "Thuesday";
					break;
					case 5:
						return "Friday";
					break;
					case 6:
						return "Saturday";
					break;
					case 7:
						return "Sunday";
					break;
				 }
			},
			getHour(){
				var d = new Date();
				return d.getHours();
			},
			getMinute(){
				var d = new Date();
				if(d.getMinutes() < 10) return "0" + d.getMinutes();
				else return d.getMinutes();
			},
			getDescription(){
				const capitalized = this.description.charAt(0).toUpperCase() + this.description.slice(1);
				return capitalized;
			},
			getDegrees(){
				return Math.ceil(this.degrees - 273.15);
			},
			getImage(){
				const Furl = "http://openweathermap.org/img/wn/";
				const Surl = "@2x.png";
				console.log(Furl+this.icon+Surl);
				return Furl+this.icon+Surl;
			},
			getWindSpeed(){
				return this.windSpeed * 3.6;
			}
		}
  	}
</script>